body {
	margin: 0;
	overflow-x: hidden;
	background: #000;
	font-family: "Open Sans", sans-serif;
	line-height: 1.4;
	font-size: 1rem;
	color: #fcfcfc;
}
* {
	box-sizing: border-box;
}
.bullets {
	margin-top: 0;
	display: flex;
	flex-wrap: wrap;
	justify-content: inherit;
	transition: padding 0.3s ease-in-out;
}
.bullet {
	position: relative;
	width: 2rem;
	padding: 1rem;
	margin-right: 0.5rem;
	cursor: pointer;
}
.bullet:after {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 100%;
	height: 4px;
	border-top: 4px solid #505152;
	transition: border-color 0.3s ease-in-out;
}
.bullet:last-child {
	margin-right: 0;
}
.bullet.bullet_active:after {
	border-color: #fff;
}



